.products {


    .product {
        display: flex;

        .details {
            width  : 75vw;
            height : 15vh;
            display: flex;

            .title-description {
                text-align: left;

                p {
                    font-size: 12px;
                }
            }

            img {
                height: 100%;
            }
        }

        .user-preferences {
            width: 25vw;

            .add-to-cart {
                margin-top: 20px;
            }

            .quantity-modifier {
                display       : flex;
                flex-direction: column;
                text-align    : center;
                align-items   : center;

                button.remove {
                    margin: 10px 0px;
                }

                .quantity {
                    display: flex;

                    button {
                        margin: 0 5px;
                    }

                    .user-input-quantity {
                        width     : 30px;
                        text-align: center;
                    }
                }
            }

            .total-amount {
                margin-top: 10px;
                border    : 1px solid black;
            }
        }

        margin-bottom: 20px;
        border       : 1px solid silver;

        padding : 0.5rem;


    }
}