.cart {

    width      : 100%;
    background : lightgoldenrodyellow;
    text-align : right;
    padding-top: 10px;
    padding    : 10px;
    background : lightblue;


    .peek-space-enalber {
        cursor: pointer;
    }

    .cart-peek {
        position  : absolute;
        width     : 70vw;
        height    : 30vh;
        background: #e9e9e9;
        right     : 5px;
        visibility: hidden;
        top       : 50px;
        font-size : 0.7rem;


        .no-items-instruction {
            text-align: center;
            margin-top: 14vh;
            font-size : 0.9rem;
        }

        .close-icon-top-right {
            position : absolute;
            right    : 5px;
            top      : -5px;
            font-size: 1.2rem;
            cursor   : pointer;
        }

        .cart-peek-item {
            width      : 100%;
            height     : 10vh;
            background : white;
            float      : left;
            text-align : left;
            border     : 1px solid silver;
            display    : flex;
            align-items: center;
            padding    : 5px;


            padding   : 10px;
            background: white;

            &:first-child {
                margin-top: 20px;
            }

            .item-description {
                width       : 100%;
                text-align  : left;
                padding-left: 20px;
            }

            .total-cost {
                text-align: right;
                display   : flex;
                width     : 20%;
            }
        }
    }
}